// part B

import React, { useState, useEffect } from 'react'
import noteService from './services/notes'
const App = () => {
  const [ persons, setPersons ] = useState([]) 
  const [ newName, setNewName ] = useState('')
  const [ newNumber, setNewNumber ] = useState(0)
  const[search, setSearch] = useState('')
  const[bs,setBs] = useState(false)
  useEffect(() => {
    console.log('effect')
    noteService.getAll()
      .then(response => {
        console.log('promise fulfilled')
        setPersons(response.data)

      })
  }, [])
  const changeName = (event) =>{
    setNewName(event.target.value)
  }
  const changeNumber = (event) =>{
    setNewNumber(event.target.value)
  }
  const changeSearch = (event) =>{
    setSearch(event.target.value)
  }
  const DeleteButton = ({deleteFunction})=>{
    return(
      <button onClick = {deleteFunction}>delete</button>
    )
  }
  const searchT = (event) =>{
    event.preventDefault()
    if(bs){
      setBs(false)
    }
    else{
      setBs(true)
    }
  }
  const addName = (event) => {
    event.preventDefault()
    console.log('success')
    const personObject = {
      name: newName,
      number: newNumber,
      id:persons.length+1
    }
    const checkArr = persons.filter(pe => pe.name === personObject.name)
    if(checkArr.length>=1){
      noteService.update(checkArr[0].id,personObject)
      .then(response=>{
        setPersons(persons.filter(pe=>checkArr[0].id!==pe.id).concat(response.data))
        setNewName('')
        setNewNumber('')
      })
    }
    else{ 
    noteService.create(personObject)
      .then(response => {
        setPersons(persons.concat(response.data))
        setNewName('')
        setNewNumber(0)
      })
      setNewName('')
      setNewNumber(0)
    }
  }
  const DeleteName = (id) =>{
    if(window.confirm("Do you really want to delete?")){
    noteService.deletion(id)
    .then(response =>{
      const newPerson = persons.filter(pe => pe.id!==id)
      setPersons(newPerson)
    })
  }

  }
  return (
    <div>
      <h2>Search</h2>
      <form onSubmit = {searchT}>
        <div>
          search:<input onChange = {changeSearch}/>
        </div>
        <div>
          <button type = "submit">Search</button>
        </div>
      </form>
      <h2>Phonebook</h2>
      <form onSubmit = {addName}>
        <div>
          name: <input onChange = {changeName}/>
        </div>
        <div>
          number: <input onChange = {changeNumber}/>
        </div>
        <div>
          <button type="submit">add</button>
        </div>
      </form>
      <h2>Numbers</h2>
      {bs? persons.filter(pe=>search.toLowerCase() === pe.name.toLowerCase()).map(ps => <div key = {ps.id}><p>{ps.name} : {ps.number}</p><DeleteButton deleteFunction = {()=>DeleteName(ps.id)}/></div>) : persons.map(pe => <div key = {pe.id}><p>{pe.name} : {pe.number}</p><DeleteButton deleteFunction = {()=>DeleteName(pe.id)}/></div>)}
    </div>
  )
}

export default App

